
import {mapMutations} from 'vuex'
import Flexbox from 'vux/src/components/flexbox/flexbox.vue'
import FlexboxItem from 'vux/src/components/flexbox/flexbox-item.vue'
import XButton from 'vux/src/components/x-button/index.vue'

import { mapGetters, mapActions } from 'vuex'

export default {
components: {
Flexbox,
FlexboxItem,
XButton
},

computed: {
...mapGetters([
'cities',
'selectedCities',
]),
},

mounted() {
this.SET_HEADER({
title:this.$t('More'),
leftOption:{showBack: true}
})
},

methods: {
...mapMutations(['SET_HEADER']),
...mapActions(['toggleCity']),
checkSelect(name){
for(let city of this.selectedCities){
if(city.name === name){
return 'selector active'
}
}
return 'selector'
}
},




}



